.main-slider {
    position: relative;
    &__slide {
        height: 564px;
        background-size: cover;
        background-position: 50% 50%;
    }
    &__arrownext, &__arrowprev {
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 8px;
        width: 44px;
        height: 37px;
        color: var(--White);
        transition: all .2s;
        &:hover {
            background-color: var(--brandColor);
        }
    }
    &__arrownext {
        bottom: 20px;
        right: calc((100% - 1110px) / 2);
    }
    &__arrowprev {
        bottom: 20px;
        left: calc((100% - 1110px) / 2 + 1110px - 88px);
    }
    &::after {
        content: "";
        display: block;
        position: absolute;
        bottom: 20px;
        right: calc((100% - 1110px) / 2);
        width: 88px;
        height: 37px;
        border-radius: 8px;
        background-color: rgba(54, 54, 54, .9);
        z-index: 9;
    }
    .swiper-pagination {
        display: flex;
        left: calc((100% - 1250px) / 2 - 5px);
        bottom: 30px;
        justify-content: flex-start;
    }
    .swiper-pagination-bullet {
        display: block;
        width: 12px;
        height: 4px;
        border-radius: 0;
        background-color: var(--White);
        opacity: 1;
    }
    .swiper-pagination-bullet-active {
        background-color: var(--brandColor);
    }
    &__block {
        margin-top: 80px;
        position: relative;
        padding: 40px 40px 55px;
        width: 500px;
        box-sizing: border-box;
        background-color: rgba(17, 17, 17, .8);
        color: var(--White);
        &::before {
            content: "";
            position: absolute;
            top: 0;
            right: 20px;
            display: block;
            width: 75px;
            height: 4px;
            background-color: var(--brandColor);
        }
    }
    &__text {
        margin: 0;
        margin-top: 20px;
        font-size: 17px;
        color: var(--DarkWhite);
        line-height: 1.5;
    }
    &__btn {
        position: absolute;
        left: 40px;
        bottom: -22px;
        display: inline-block;
        font-size: 17px;
        padding: 15px 30px;
    }
}

@media(max-width: 1280px) {
    .main-slider {
        &__arrownext {
            right: 15px;
        }
        &__arrowprev {
            left: calc(100% - 15px - 88px);
        }
        .swiper-pagination {
            left: 11px;
        }
        &::after {
            right: 15px;
        }
    }
}
@media(max-width: 575px) {
    .main-slider {
        &__block {
            width: 100%;
            padding: 30px 30px 45px;
            text-align: center;
            &::before {
                right: calc(50% - 37.5px);
            }
        }
        &__btn {
            left: calc(50% - 78px);
        }
    }
}
@media(max-width: 400px) {
    .main-slider {
        &__block {
            margin-top: 40px;
        }
    }
}